.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}

@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  background-color: #000;
  opacity: 0.6;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@media (min-width: 480px) {
  .header {
    padding: 4rem;
  }
}
@media (min-width: 768px) {
  .header {
    padding: 3rem;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 2rem;
  }
}
.header .contenido-header {
  position: relative;
}

.header .contenido-header h1 {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}
.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}

.header-nosotros {
  background-image: url(../img/header_nosotros.jpg);
}

.header-proceso {
  background-image: url(../img/header_proceso.jpg);
}

.header-menu {
  background-image: url(../img/header_proceso.jpg);
}

.header-galeria {
  background-image: url(../img/header_galeria.jpg);
}

.header-contacto {
  background-image: url(../img/header_contacto.jpg);
}

.nav-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  color: #fff;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
