.barra {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .logo {
  width: 50%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
