@use 'base/mixins' as m;


.barra {
    display: flex;
    flex-direction: column;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }

    .logo {
        width: 50%;
        margin: 0 auto;

        @include m.tablet {
            width: 20rem;
        }

    }

}

